import React from "react";
import { Button, Form, Modal } from "react-bootstrap";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { patientFormStatus } from "../../../../../../../app/features/OnlinePrescriptionForm/OnlinePrescriptionForm.slice";
import { PATH } from "../../../../../../../utils/path";
import { useNavigate } from "react-router-dom";

export default function RejectedMedicationModal({ show, onClose }) {
  const { register, handleSubmit } = useForm();

  const user = JSON.parse(localStorage.getItem("family_doc_app"));
  const { patientOnlinePrescForm } = useSelector(
    (state) => state?.patientOnlinePresc
  );

  const dispatch = useDispatch();
  const navigate = useNavigate();

  function onCreateSuccess() {
    navigate(PATH.DOCTOR_PRESCRIPTION_DASHBOARD);
  }
  function submitFormStatus(submittedData) {
    const finalData = {
      patientFormStatusId:
        patientOnlinePrescForm?.data?.[0]?.patientFormStatusId,
      doctorId: user?.userId,
      formStatusId: 502,
      rejectedReason: submittedData?.reasonOfRejected,
    };
    dispatch(patientFormStatus({ finalData, onCreateSuccess }));
  }

  return (
    <>
      <Modal
        show={show}
        onHide={onClose}
        centered
        size="lg"
        className="doctor-medication-modal"
        backdrop="static"
      >
        <Form onSubmit={handleSubmit(submitFormStatus)}>
          <Modal.Header closeButton>
            <Modal.Title className="prescription-modal-title">
              Prescription
            </Modal.Title>
          </Modal.Header>
          <Modal.Body className="">
            <Form.Group className="mb-3" controlId="formBasicLabel1">
              <Form.Label className="label-primary mb-0">
                Reason of Rejection
              </Form.Label>
              <Form.Control
                as="textarea"
                placeholder="Type Here..."
                {...register("reasonOfRejected", { required: true })}
                rows={5}
                className="pt-2"
                style={{ height: "200px", whiteSpace: "pre-wrap" }}
                maxLength={250}
              />
            </Form.Group>
          </Modal.Body>
          <Modal.Footer className="d-flex justify-content-center">
            <Button
              type="submit"
              className="w-50 border-0"
              style={{ background: "hsl(240, 100%, 22.15686274509804%)" }}
            >
              Save
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>
    </>
  );
}
