import React, { useEffect } from "react";
import { Card, Col, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import { IoIosArrowForward } from "react-icons/io";
import GenericPatientCard from "../../../../components/Theme/GenericPatientCard";
import { useDispatch, useSelector } from "react-redux";
import PatientForm from "./PatientForm";
import { getPatient } from "../../../../app/features/patient/patient.slice";

function PatientPrescriptionForm() {
  const searchParams = new URLSearchParams(window.location.search);
  let patientId = searchParams.get("patientId");

  const dispatch = useDispatch();
  const { getPatientData } = useSelector((state) => state?.patient);

  useEffect(() => {
    const finalData = {
      patientId: patientId,
    };

    dispatch(getPatient(finalData));
  }, [dispatch, patientId]);

  return (
    <>
      <nav aria-label="breadcrumb">
        <ol className="breadcrumb">
          <li className="breadcrumb-item">
            <Link
              to={"/doctor/online-prescription-questionnaire"}
              className="text-decoration-none fs-5 color-99"
            >
              Online Prescription
            </Link>
          </li>
          <IoIosArrowForward className="mx-1 mt-2 color-99" />
          <li
            className="breadcrumb-item active fs-5"
            aria-current="page"
            style={{ color: "#000071" }}
          >
            Patient Form
          </li>
        </ol>
      </nav>

      <Row className="my-3 patient-details">
        <Col xl={3} lg={4} md={4}>
          <div className="shadow-sm">
            <GenericPatientCard
              name={
                getPatientData?.firstName
                  ? `${getPatientData?.firstName} ${getPatientData?.lastName}`
                  : "N/A"
              }
              genderInfo="true"
              gender={getPatientData?.gender ? getPatientData?.gender : "N/A"}
              dobInfo="true"
              dob={
                getPatientData?.dob ? getPatientData?.dob.split("T")[0] : "N/A"
              }
              locationInfo="true"
              location={
                getPatientData?.currentAddress
                  ? getPatientData?.currentAddress
                  : "N/A"
              }
              currentLocationInfo="true"
              currentLocation={
                getPatientData?.currentAddress
                  ? getPatientData?.currentAddress
                  : "N/A"
              }
              patientLocalGPInfo="true"
              patientLocalGP={
                getPatientData?.patientLocalGP
                  ? getPatientData?.patientLocalGP
                  : "N/A"
              }
              emailInfo="true"
              email={getPatientData?.email ? getPatientData?.email : "N/A"}
              pharmacyInfo="true"
              pharmacy="Clinix"
              phoneInfo="true"
              phone={
                getPatientData?.phoneNumber
                  ? getPatientData?.phoneNumber
                  : "N/A"
              }
              image={getPatientData?.imageUrl}
            />
          </div>
        </Col>
        <Col xl={9} lg={8} md={8}>
          <Card className="shadow-sm px-4 py-4 h-100">
            <PatientForm />
          </Card>
        </Col>
      </Row>
    </>
  );
}
export default PatientPrescriptionForm;
