import React, { useEffect, useState } from "react";
import { Button, Form } from "react-bootstrap";
import { getPatientPresForm } from "../../../../../app/features/PatientOnlinePrescription/PatientOnlinePrescription.slice";
import { useDispatch, useSelector } from "react-redux";
import FullScreenLoader from "../../../../../authorization/FullScreenLoader";
import { useForm } from "react-hook-form";
import { patientFormStatus } from "../../../../../app/features/OnlinePrescriptionForm/OnlinePrescriptionForm.slice";
import { useLocation, useNavigate } from "react-router-dom";
import { PATH } from "../../../../../utils/path";
import RejectedMedicationModal from "../Medications/MedicationModal/RejectedForm/index.jsx";

function PatientForm() {
  const { handleSubmit } = useForm();

  const [rejectedReason, setRejectedReason] = useState(false);

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const location = useLocation();

  const user = JSON.parse(localStorage.getItem("family_doc_app"));

  const { patientOnlinePrescForm, isSuccess, isLoading, isError } = useSelector(
    (state) => state?.patientOnlinePresc
  );
  const searchParams = new URLSearchParams(window.location.search);

  const paramsObject = {};

  searchParams.forEach((value, key) => {
    paramsObject[key] = parseInt(value, 10);
  });

  useEffect(() => {
    const finalData = {
      patientId: paramsObject?.patientId,
      formId: paramsObject?.formId,
      patientFormStatusId: location?.state?.patientFormStatusId,
      patientFormAttempt: location?.state?.patientFormAttempt,
    };
    dispatch(getPatientPresForm(finalData));
  }, [
    dispatch,
    paramsObject?.patientId,
    paramsObject?.formId,
    location?.state?.patientFormStatusId,
    location?.state?.patientFormAttempt,
  ]);

  function onCreateSuccess() {
    navigate(
      `${PATH.DOCTOR_MEDICATION_FORM}?patientId=${paramsObject?.patientId}&formId=${paramsObject?.formId}`,
      {
        state: patientOnlinePrescForm?.data?.[0]?.patientFormStatusId,
      }
    );
  }

  function submitFormStatus() {
    const finalData = {
      patientFormStatusId:
        patientOnlinePrescForm?.data?.[0]?.patientFormStatusId,
      doctorId: user?.userId,
      formStatusId: 503,
      rejectedReason: "",
    };
    dispatch(patientFormStatus({ finalData, onCreateSuccess }));
  }

  return (
    <>
      <div className="my-2">
        <h5 className="px-2 py-2 fw-bold header-border">
          Patient Prescription Form
        </h5>

        {isLoading ? (
          <FullScreenLoader />
        ) : isSuccess ? (
          <Form className="mx-3" onSubmit={handleSubmit(submitFormStatus)}>
            {patientOnlinePrescForm?.data?.length > 0 ? (
              <>
                {patientOnlinePrescForm?.data?.map((section, sectionIndex) => (
                  <>
                    <Form.Group
                      className="mb-3"
                      controlId={`formQuestion${sectionIndex}`}
                    >
                      <Form.Label className="label-primary mt-2 fw-bold">
                        <span className="fw-bold">
                          Q.{`${sectionIndex + 1}.`}{" "}
                        </span>
                        {section.question}
                      </Form.Label>

                      {section?.answerType === "Radio" ? (
                        <>
                          {section?.formAnswersList?.map(
                            (value, optionIndex) => (
                              <p>
                                <span className="fw-bold">Ans: </span>
                                {value?.answerLabel
                                  ? value?.answerLabel
                                  : "N/A"}
                              </p>
                            )
                          )}
                        </>
                      ) : section?.answerType === "TextBox" ? (
                        <>
                          {section?.formAnswersList?.map(
                            (value, valueIndex) => (
                              <p>
                                <span className="fw-bold">Ans: </span>
                                {value?.textAnswerValue
                                  ? value?.textAnswerValue
                                  : "N/A"}
                              </p>
                            )
                          )}
                        </>
                      ) : section?.answerType === "Checkbox" ? (
                        <div className="d-flex">
                          <span className="fw-bold me-1">Ans:</span>
                          <p className="d-inline-grid mb-0">
                            {section?.formAnswersList?.map(
                              (option, optionIndex) => (
                                <span key={optionIndex} className="mb-0">
                                  {section.formAnswersList.length > 1 &&
                                    `${optionIndex + 1}. `}
                                  {option?.answerLabel
                                    ? option?.answerLabel
                                    : "N/A"}
                                </span>
                              )
                            )}
                          </p>
                        </div>
                      ) : section?.answerType === "Single Check Box" ? (
                        <div className="d-flex">
                          <span className="fw-bold me-1">Ans:</span>
                          <p className="d-inline-grid mb-0">
                            <span className="mb-0">Yes</span>
                          </p>
                        </div>
                      ) : null}
                    </Form.Group>
                  </>
                ))}

                {location?.state?.formStatus !== "Accepted" &&
                !location?.state?.isPrescriptionAdded ? (
                  <div className="d-flex justify-content-end">
                    <Button
                      className="Admin-Modal-CancelBtn fw-bold me-2"
                      onClick={() => setRejectedReason(true)}
                    >
                      Reject
                    </Button>
                    <Button
                      variant="primary"
                      type="submit"
                      className="Admin-Add-btn fw-bold"
                    >
                      Accept
                    </Button>
                  </div>
                ) : location?.state?.formStatus === "Accepted" &&
                  !location?.state?.isPrescriptionAdded ? (
                  <div className="d-flex justify-content-end">
                    <Button
                      variant="primary"
                      type="button"
                      onClick={onCreateSuccess}
                      className="Admin-Add-btn fw-bold"
                    >
                      Create Prescription
                    </Button>
                  </div>
                ) : null}
              </>
            ) : (
              <p className="record-message">No Records to Display</p>
            )}
          </Form>
        ) : isError ? (
          <span className="text-danger fst-italic">Network Error</span>
        ) : (
          <p className="record-message">No Records to Display</p>
        )}
      </div>
      <RejectedMedicationModal
        show={rejectedReason}
        onClose={() => setRejectedReason(false)}
      />
    </>
  );
}

export default PatientForm;
